<template>
  <div class="mapbox-wrapper">
    <MglMap
      :access-token="accessToken"
      :map-style.sync="options.style"
      :center="options.center"
      :zoom="options.zoom"
      :pitch="options.pitch"
      :bearing="options.bearing"
      :container="options.container"
      :attribution-control="false"
      @load="onLoad"
    >
      <MglNavigationControl position="bottom-right" />
    </MglMap>

    <div :id="topRightDiv">
      <top-right-control
        :search-toggle.sync="searchToggle"
        :filter-toggle.sync="filterToggle"
        :grid-toggle.sync="gridToggle"
      />
    </div>

    <b-collapse
      v-model="searchToggle"
      class="searchbox-wrapper"
    >
      <SearchBox
        :search-close.sync="searchToggle"
      />
    </b-collapse>
  </div>
</template>

<script>
import Mapbox from 'mapbox-gl'
import {
  MglMap,
  MglNavigationControl,
} from 'vue-mapbox'
import TopRightControl from './TopRightControl.vue'
import SearchBox from './SearchBox.vue'

export default {
  components: {
    MglMap,
    MglNavigationControl,
    SearchBox,
    TopRightControl,
  },
  data() {
    return {
      accessToken: process.env.VUE_APP_ACCESS_TOKEN, // your access token. Needed if you using Mapbox maps
      options: {
        style: 'mapbox://styles/mapbox/light-v10',
        center: [55.2752798, 25.1919927],
        zoom: 15.5,
        pitch: 65,
        bearing: -17.6,
        container: 'map',
        antialias: true,
      }, // your map style

      topRightDiv: 'top-right-components',
      searchToggle: false,
      filterToggle: false,
      gridToggle: false,
    }
  },

  created() {
    // We need to set mapbox-gl library here in order to use it in template
    this.mapbox = Mapbox
  },
  methods: {
    onLoad({ map, component }) {
      // Insert the layer beneath any symbol layer.
      const { layers } = map.getStyle()
      const labelLayerId = layers.find(
        layer => layer.type === 'symbol' && layer.layout['text-field'],
      ).id

      // The 'building' layer in the Mapbox Streets
      // vector tileset contains building height data
      // from OpenStreetMap.
      map.addLayer(
        {
          id: 'add-3d-buildings',
          source: 'composite',
          'source-layer': 'building',
          filter: ['==', 'extrude', 'true'],
          type: 'fill-extrusion',
          minzoom: 14,
          paint: {
            'fill-extrusion-color': '#aaa',
            // Use an 'interpolate' expression to
            // add a smooth transition effect to
            // the buildings as the user zooms in.
            'fill-extrusion-height': [
              'interpolate',
              ['linear'],
              ['zoom'],
              14,
              0,
              15.05,
              ['get', 'height'],
            ],
            'fill-extrusion-base': [
              'interpolate',
              ['linear'],
              ['zoom'],
              14,
              0,
              15.05,
              ['get', 'min_height'],
            ],
            'fill-extrusion-opacity': 0.6,
          },
        },
        labelLayerId,
      )

      // Add full screen control
      map.addControl(new this.mapbox.FullscreenControl(), 'bottom-right')

      // Append search box component
      document.getElementsByClassName('mapboxgl-ctrl-top-right')[0].appendChild(document.getElementById(this.topRightDiv))

      // Map resize
      map.resize()
    },
  },
}
</script>

